"use strict";
//**–¥–∞—Ç–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω–∏–∫–µ */
let today = new Date();
let year = today.getFullYear();
let monthNow = today.getMonth() + 1;

const getDay = () => {
  let daysArr = [
    "–í–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ",
    "–ü–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫",
    "–í—Ç–æ—Ä–Ω–∏–∫",
    "–°—Ä–µ–¥–∞",
    "–ß–µ—Ç–≤–µ—Ä–≥",
    "–ü—è—Ç–Ω–∏—Ü–∞",
    "–°—É–±–±–æ—Ç–∞",
  ];
  return daysArr[today.getDay()];
};

document.querySelector(".myDay").innerHTML = getDay();
const getDat = () => {
  let montsArr = [
    "–Ø–Ω–≤–∞—Ä—è",
    "–§–µ–≤—Ä–∞–ª—è",
    "–ú–∞—Ä—Ç–∞",
    "–ê–ø—Ä–µ–ª—è",
    "–ú–∞—è",
    "–ò—é–Ω—è",
    "–ò—é–ª—è",
    "–ê–≤–≥—É—Å—Ç–∞",
    "–°–µ–Ω—Ç—è–±—Ä—è",
    "–û–∫—Ç—è–±—Ä—è",
    "–ù–æ—è–±—Ä—è",
    "–î–µ–∫–∞–±—Ä—è",
  ];
  return montsArr[today.getMonth()];
};

document.querySelector(".myData").innerHTML =
  today.getDate() + " " + getDat() + " " + year + " " + "–≥.";

//* –±–µ–≥—É—â–µ–µ –≤—Ä–µ–º—è –Ω–∞ –º–æ–±–∏–ª—å–Ω–∏–∫–µ**//
setInterval(myTimer, 1000);
function myTimer() {
  let today = new Date();
  let second = today.getSeconds();
  let minut = today.getMinutes();
  if (+second < 10) {
    second = "0" + second;
  }
  if (+minut < 10) {
    minut = "0" + minut;
  }

  document.getElementById("myTime").innerHTML =
    today.getHours() + ":" + minut + ":" + second;
}

//**–∫–∞–ª–µ–Ω–¥–∞—Ä–∏–∫ –Ω–∞ –º–æ–±–∏–ª—å–Ω–∏–∫–µ**/
const getmonth = () => {
  let monthArr = [
    "–Ø–Ω–≤–∞—Ä—å",
    "–§–µ–≤—Ä–∞–ª—å",
    "–ú–∞—Ä—Ç",
    "–ê–ø—Ä–µ–ª—å",
    "–ú–∞–π",
    "–ò—é–Ω—å",
    "–ò—é–ª—å",
    "–ê–≤–≥—É—Å—Ç",
    "–°–µ–Ω—Ç—è–±—Ä—å",
    "–û–∫—Ç—è–±—Ä—å",
    "–ù–æ—è–±—Ä—å",
    "–î–µ–∫–∞–±—Ä—å",
  ];
  return monthArr[today.getMonth()];
};
document.querySelector(".month").innerHTML =
  getmonth() + " " + year + " " + "–≥.";

//**–¢–∞–±–ª–∏—Ü–∞-–º–µ—Å—è—Ü**//
function createCalendar(elem, year, month) {
  let mon = month - 1; // –º–µ—Å—è—Ü—ã –≤ JS –∏–¥—É—Ç –æ—Ç 0 –¥–æ 11, –∞ –Ω–µ –æ—Ç 1 –¥–æ 12
  let d = new Date(year, mon);

  let table =
    "<table><tr><th>–ø–Ω</th><th>–≤—Ç</th><th>—Å—Ä</th><th>—á—Ç</th><th>–ø—Ç</th><th>—Å–±</th><th>–≤—Å</th></tr><tr>";

  // –ø—Ä–æ–±–µ–ª—ã –¥–ª—è –ø–µ—Ä–≤–æ–≥–æ —Ä—è–¥–∞ –¥–æ –ø–µ—Ä–≤–æ–≥–æ –¥–Ω—è –º–µ—Å—è—Ü–∞
  // * * * 1  2  3  4
  for (let i = 0; i < gettDay(d); i++) {
    table += "<td></td>";
  }

  // —Å–æ–∑–¥–∞—ë–º <td> —è—á–µ–π–∫–∏ –∫–∞–ª–µ–Ω–¥–∞—Ä—è —Å –¥–∞—Ç–∞–º–∏
  while (d.getMonth() == mon) {
    table += "<td>" + d.getDate() + "</td>";

    if (gettDay(d) % 7 == 6) {
      // –≤—Å, –ø–æ—Å–ª–µ–¥–Ω–∏–π –¥–µ–Ω—å - –ø–µ—Ä–µ–≤–æ–¥ —Å—Ç—Ä–æ–∫–∏
      table += "</tr><tr>";
    }

    d.setDate(d.getDate() + 1);
  }
  // –¥–æ–±–∏–≤–∞–µ–º —Ç–∞–±–ª–∏—Ü—É –ø—É—Å—Ç—ã–º–∏ —è—á–µ–π–∫–∞–º–∏, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ
  if (gettDay(d) != 0) {
    for (let i = gettDay(d); i < 7; i++) {
      table += "<td></td>";
    }
  }
  // –∑–∞–∫—Ä—ã–≤–∞–µ–º —Ç–∞–±–ª–∏—Ü—É
  table += "</tr></table>";
  elem.id = "tbl";
  elem.innerHTML = table;
}

function gettDay(date) {
  // –ø–æ–ª—É—á–∞–µ–º –Ω–æ–º–µ—Ä –¥–Ω—è –Ω–µ–¥–µ–ª–∏, –æ—Ç 0 (–ø–Ω) –¥–æ 6 (–≤—Å)
  let day = date.getDay();
  if (day == 0) day = 7; // –¥–µ–ª–∞–µ–º –≤–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ (0) –ø–æ—Å–ª–µ–¥–Ω–∏–º –¥–Ω–µ–º
  return day - 1;
}

createCalendar(calendar, year, monthNow);

// ***TODO list***/
let ul = document.querySelector("#list");
let input = document.getElementById("toDoInput"); 
let addBtn = document.getElementById("addBtn");
input.addEventListener("keypress", validationPress);
addBtn.addEventListener("click", validation);

let items = JSON.parse(localStorage.getItem("ToDo")) || [];

////*–≥–µ–Ω–µ—Ä–∞—Ü–∏—è ID*////
let setId = new Set();

function creatId() {
  let newId = Math.floor(Math.random() * 10000000);
  if (!setId.has(newId)) {
    setId.add(newId);
    return newId;
  } else {
    return creatId();
  }
}

function validationPress(event) {
  if (event.keyCode == 13) {
    if (input.value === "" || input.value === null) {
      alert("–í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ");
    } else {
      createObj();
    }
  }
}

function validation() {
  if (input.value === "" || input.value === null) {
    alert("–í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ");
  } else {
    createObj();
  }
}

function createObj() {
  let idtodo = creatId();
  let todoItem = { value: input.value, checked: false, id: idtodo };
  items.push(todoItem);
  localStorage.setItem("ToDo", JSON.stringify(items));
  input.value = "";
  createTodo();
}

function createTodo() {
  ul.innerHTML = "";
  for (let i = 0; i < items.length; i++) {
    let li = document.createElement("li");
    li.prepend(items[i].value);
    li.setAttribute("id", +items[i].id);
    li.className = items[i].checked ? "checked" : "";

    let deleteIcon = document.createElement("span");
    deleteIcon.className = "icon__delit";
    li.append(deleteIcon);
    ul.prepend(li);
    setId.add(items[i].id); //–Ω—É–∂–Ω–æ –ø—Ä–∏ –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  }
}

ul.addEventListener("click", function (event) {
  if (event.target.tagName === "LI") {
    event.target.classList.toggle("checked");
    event.stopPropagation();
    for (let i = 0; i < items.length; ++i) {
      if (items[i].id == event.target.getAttribute("id")) {
        items[i].checked = !items[i].checked;
        localStorage.setItem("ToDo", JSON.stringify(items));
      }
    }
  } else if (event.target.tagName === "SPAN") {
    event.target.parentNode.remove();
    event.stopPropagation();
    let iD = +event.target.parentNode.getAttribute("id");
    items = items.filter((val) => +val.id != iD);
    localStorage.setItem("ToDo", JSON.stringify(items));
  }
});

if (localStorage.getItem("ToDo")) {
  createTodo();
}
